.avatar-sidebar {
    background-color: #f1f1f1;
    width: 300px;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: sans-serif;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    margin: 20px;
  }
  
  .avatar-sidebar__header {
    display: flex;
    align-items: center;
    margin: 20px;
  }
  
  .avatar-sidebar__avatar {
    border-radius: 20%;
    height: 100px;
    margin-right: 10px;
    background-color: #ddd;
    width: 100px;
  }
  
  .avatar-sidebar__description {
    display: flex;
    flex-direction: column;
    align-items: center;
    }

  .avatar-sidebar__name {
    margin: 0;
    font-size: 24px;
    color: #333;
  }
  
  .avatar-sidebar__foldable {
    align-items: center;
    width: 100%;
  }
  
  .avatar-sidebar__foldable__header {
    display: flex;
    justify-content: center;
    cursor: pointer;
    padding: 10px 20px;
    border-radius: 0;
    transition: background-color 0.2s ease;
    background-color: #ddd;
  }
  
  .avatar-sidebar__foldable__header:hover {
    background-color: #bbb;
  }
  
  .avatar-sidebar__foldable__title {
    margin: 0;
    font-size: 20px;
    color: #333;
    align-self: center;
  }
  
  .avatar-sidebar__foldable__list {
    margin: 0;
    padding: 0;
    justify-content: center;
    list-style: none;
    display: none;
  }
  
  .avatar-sidebar__foldable__list--visible {
    display: block;
  }
  
  .avatar-sidebar__foldable__list li {
    margin-bottom: 0;
    justify-content: center;
    font-size: 18px;
    color: #666;
    padding: 10px 20px;
    border-radius: 0;
    transition: background-color 0.2s ease;
    background-color: #eee;
  }
  
  .avatar-sidebar__foldable__list li:hover {
    background-color: #ddd;
  }

  .avatar-sidebar__foldable__list li a {
    color: inherit;
    text-decoration: none;
  }
